import React from 'react'
import "./Banner.css"

class BannerProps {
    /** @type {string} */
    bgColor;
    /** @type {string} */
    fgColor;
    /** @type {string} */
    text;
    /** @type {string} */
    url;
}

/** @param {BannerProps} bannerProps  */
export default function Banner(bannerProps) {
    const bgColor = bannerProps.bgColor ?? 'var(--accent-color)';
    const fgColor = bannerProps.fgColor ?? 'var(--light-color)';
    const text = bannerProps.text ?? '';

    const style = {
        backgroundColor: bgColor,
        color: fgColor
    };

  return (
    <div className='banner-wrapper'>
        <div className='banner' style={style}>
            <div className='banner-text'>
                <span>{text}</span>
            </div>
        </div>
    </div>
  )
}
