class Item {
    /** @type {Date} */
    createdAt;

    /** @type {Date} */
    updatedAt;

    /** @type {string} */
    barcode;

    /** @type {string} */
    name;

    /** @type {string} */
    summary;

    /** @type {number} */
    price;

    /** @type {string} */
    category;

    /** @type {number} */
    quantity;

    /** @type {number} */
    usedQuantity;

    /** @type {boolean} */
    active = true;
}

export default Item;