import React from "react";
import Item from "models/item";

export default function EditItem({ item }) {
  const [isOpen, setIsOpen] = React.useState(false);
  const [editedItem, setEditedItem] = React.useState(item ?? new Item());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const categories = [
    "book",
    "merchandise",
  ]

  const handleSave = () => {
    // Logic to save the edited item
    setIsOpen(false);
  };

  const isNewItem = !item || !item.barcode;
  const buttonText = isNewItem ? "Create Item" : "Edit Item";

  if (isOpen) {
    return (
      <div className="modal">
        <div className="modal-content">
          <div className="modal-header">
            <h2>{buttonText}</h2>
            <span className="modal-close" onClick={() => setIsOpen(false)}>
              &times;
            </span>
          </div>
          <form>
            <div className="modal-body">
              <label>
                Barcode:
                <input
                  type="text"
                  name="barcode"
                  value={editedItem.barcode}
                  onChange={handleChange}
                />
              </label>
              <label>
                Name:
                <input
                  type="text"
                  name="name"
                  value={editedItem.name}
                  onChange={handleChange}
                />
              </label>
              <label>
                Summary:
                <input
                  type="text"
                  name="summary"
                  value={editedItem.summary}
                  onChange={handleChange}
                />
              </label>
              <label>
                Price:
                <input
                  type="number"
                  name="price"
                  value={editedItem.price}
                  onChange={handleChange}
                />
              </label>
              <label>
                Category:
                <select
                  name="category"
                  value={editedItem.category}
                  onChange={handleChange}>
                    <option value="">Select Category</option>
                    {categories.map((category) => (
                      <option key={category} value={category}>
                        {category}
                      </option>
                    ))}
                  </select>
              </label>
              <label>
                Quantity:
                <input
                  type="number"
                  name="quantity"
                  value={editedItem.quantity}
                  onChange={handleChange}
                />
              </label>
              <label>
                Used Quantity:
                <input
                  type="number"
                  name="usedQuantity"
                  value={editedItem.usedQuantity}
                  onChange={handleChange}
                />
              </label>

              {editedItem.category === "book" && (
                <>
                  <label>
                    Author:
                    <input
                      type="text"
                      name="author"
                      value={editedItem.author}
                      onChange={handleChange}
                    />
                  </label>
                  <label>
                    Publisher:
                    <input
                      type="text"
                      name="publisher"
                      value={editedItem.publisher}
                      onChange={handleChange}
                    />
                  </label>
                  <label>
                    ISBN:
                    <input
                      type="text"
                      name="isbn"
                      value={editedItem.isbn}
                      onChange={handleChange}
                    />
                  </label>
                </>
              )}

              <label>
                Active:
                <input
                  type="checkbox"
                  name="active"
                  checked={editedItem.active}
                  onChange={(e) =>
                    setEditedItem((prevItem) => ({
                      ...prevItem,
                      active: e.target.checked,
                    }))
                  }
                />
              </label>
            </div>
            <div className="modal-footer">
              <button type="button" onClick={handleSave}>
                Save
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }

  return (
    <div>
      <button onClick={() => setIsOpen(true)}>{buttonText}</button>
    </div>
  );
}
