import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useMsal } from '@azure/msal-react';

const RequireEmployeeAuth = ({ children }) => {
    const { accounts } = useMsal();
    const location = useLocation();
  
    const isAuthenticated = accounts.length > 0;
    const user = isAuthenticated ? accounts[0] : null;
    const isFromOrganization = user && user.idTokenClaims.tid === process.env.REACT_APP_BUSINESS_TENANT_ID;

    if(isAuthenticated && isFromOrganization) {
      return children;
    } else {
        return <Navigate to="/access-denied" state={{ from: location }} />;
    }
  };

export default RequireEmployeeAuth;