import React from "react";
import "./Footer.css";
import Connect from "components/Connect";
import { useMsal, useIsAuthenticated } from "@azure/msal-react";
import { NavLink } from "react-router-dom";

export default function Footer() {
  const isAuthenticated = useIsAuthenticated();
  const { accounts } = useMsal();
  const userIsInOrganization =
    isAuthenticated &&
    accounts.some(
      (account) =>
        account.idTokenClaims &&
        account.idTokenClaims["tid"] ===
          process.env.REACT_APP_BUSINESS_TENANT_ID
    );

  return (
    <footer>
      <div className="footer-top">
        {userIsInOrganization ? (
          <div className="footer-column">
            <h4>Employees Only</h4>
            <ul>                
              <li><NavLink to="/management/inventory">Inventory Management</NavLink></li>
            </ul>
          </div>
        ) : (
          <></>
        )}
        <div className="footer-column">
          <h4>About</h4>
          <ul>
            <li>Our Story</li>
            <li>Meet the Booksellers</li>
            <li>Contact Us</li>
          </ul>
        </div>
        <div className="footer-column">
          <h4>Customer Service</h4>
          <ul>
            <li>Help</li>
            <li>Returns</li>
            <li>Shipping</li>
            <li>Terms of Service</li>
            <li>Privacy Policy</li>
          </ul>
        </div>
        <div className="footer-column">
          <h4 style={{ marginBottom: "1em" }}>Connect</h4>
          <Connect />
        </div>
      </div>

      <div className="footer-bottom">
        &copy; {new Date().getFullYear()} Paperback Porch, LLC
      </div>
    </footer>
  );
}
