import React from "react";
import "models/item";
import "./InventoryTable.css";
import EditItem from "./EditItem";

class InventoryTableProps {
  /** @type {Item[]} */
  items = [];
}

/** @param {InventoryTableProps} props  */
export default function InventoryTable(props) {
  return (
    <div>
      <div className="inventory-table">
        <div className="table-header">
          <div className="table-cell">Barcode</div>
          <div className="table-cell">Category</div>
          <div className="table-cell">Name</div>
          <div className="table-cell">Summary</div>
          <div className="table-cell">Price</div>
          <div className="table-cell">Quantity</div>
          <div className="table-cell">Used Quantity</div>
          <div className="table-cell">Active</div>
          <div className="table-cell">
            <EditItem />
          </div>
        </div>
        {props.items.map((item, index) => (
          <div className="table-row" key={index}>
            <div className="table-cell">{item.barcode}</div>
            <div className="table-cell">{item.category}</div>
            <div className="table-cell">{item.name}</div>
            <div className="table-cell">{item.summary}</div>
            <div className="table-cell">{item.price}</div>
            <div className="table-cell">{item.quantity}</div>
            <div className="table-cell">{item.usedQuantity}</div>
            <div className="table-cell">{item.active ? "Yes" : "No"}</div>
            <div className="table-cell">
              <EditItem item={item} />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
